.App {
  font-family: "Outfit", sans-serif;
  text-align: center;
  display: grid;
  grid-template-rows: 70px 1fr;
  grid-template-columns: 1fr minmax(50px, 200px) minmax(50px, 200px) minmax(50px, 200px);
  height: 100vh;
}
body {
  height: 100vh;
}
.header {
  background-color: #00adb5;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header h1 {
  margin-left: 1rem;
  font-size: 1.5rem;
}
.header h2 {
  font-size: 1.5rem;
  font-weight: 500;
}
.header:hover {
  cursor: pointer;
  opacity: 0.95;
}

.logo {
  display: flex;
  justify-content: left;
}
.logo img {
  max-width: 80%;
  max-height: 80%;
  margin-left: 1rem;
}
.content {
  grid-row: 2/3;
  grid-column: 1/5;
  background-color: white;
}
.center {
  width: 100%;
  height: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
  padding-top: 3rem;
}
/* Form  */
.form {
  display: flex;
  flex-direction: column;
  width: 200px;
  max-width: 350px;
  min-width: 250px;
  border: 0px solid #393e46;
  border-radius: 15px;
  padding: 2rem 2.5rem 2.5rem 2.5rem;
  background-color: #eeeeee;
}
.form h2 {
  text-align: center;
  font-size: 2rem;
  margin: 1rem;
}
.form h3 {
  text-align: center;
}
.form input {
  box-sizing: border-box;
  width: 100%;
  height: 2rem;
}
.blue-submit-button {
  display: block;
  text-align: center;
  margin: auto;
  margin-top: 1.5rem;
  width: 100%;
  height: 2.5rem;
  background-color: #00adb5;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 1.2rem;
}
.blue-submit-button:hover {
  opacity: 0.8;
}
.form button:hover {
  cursor: pointer;
}
.team {
  width: 500px;
  height: 500px;
}
.team p {
  margin-top: 0.3rem;
  margin-bottom: 0.3rem;
}
.team h2 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.extra {
  height: 400px;
}
.popup-out {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.6); /* Black w/ opacity */
  overflow: auto;
}
.popup-in {
  position: relative;
  color: black;
  border: 0px solid #393e46;
  border-radius: 15px;
  padding: 2rem 2.5rem 2.5rem 2.5rem;
  background-color: #eeeeee;
  margin: 10px;
}
.popup-close-icon {
  position: absolute;
  right: 0.5rem;
  top: -1.5rem;
  font-weight: 900;
  font-size: 1.65rem;
  color: red;
}
.popup-close-icon:hover,
.popup-close-icon:focus {
  opacity: 0.5;
  text-decoration: none;
  cursor: pointer;
}
